import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'app-side-details',
  templateUrl: './side-details.component.html',
  styleUrls: ['./side-details.component.scss']
})
export class SideDetailsComponent implements OnInit {

  constructor() { }

  @Output() showElement = new EventEmitter();
  
  @Input('showMenu') showMenu

  showResults;
  showMoreResults;
  showDetails;

  emit(showResults, showMoreResults, showDetails) {
    let navigationOption = {
      showResults: showResults,
      showMoreResults: showMoreResults,
      showDetails: showDetails
    }
    
    console.log(navigationOption, 'navigationOption')
    setTimeout(() => {
      this.showElement.emit(navigationOption)
    }, 200);
  }


  ngOnInit() {
  }

}
